<template>
	<div class="set_nickname">
		<van-cell-group>
			<van-field
			v-model="name"
			label="姓名"
			:error="!!$vuelidation.error('name')" />
		</van-cell-group>

		<div class="bottom_btn">
			<van-button size="large" type="danger" @click="saveNick">保存</van-button>
		</div>
	</div>
</template>


<script>
	import { USER_PROFILE } from '@/api/user';
	import valid from '@/api/user';

	export default {
		data(){
			return {
				name: ""
			}
		},

		vuelidation: {
			data: {
				name: {
					required: {
						msg(){
							return "请填写姓名"
						}
					}
				}
			}
		},

		created(){
			this.getNick();
		},

		methods: {
			getNick(){
				this.name = localStorage.getItem('name') || '';
			},
			saveNick(){
				if(this.mixValid()){
					this.$reqPut(USER_PROFILE, {name: this.name}).then(res => {
						this.saveSuccess(res);
					}).catch(err => {
            this.saveSuccess(err);
          })
				}
			},

      saveSuccess(res){
			  if(!res.data){
			    return;
        }
        if(res.data.code != 0){
			    let msg = res.data.msg ? res.data.msg : "保存失败";
			    Toast.fail(msg);
			    return;
        }
        localStorage.setItem('name', this.name);
        return this.$dialog.alert({message: '保存成功'}).then(() => {
          this.$router.go(-1);
        });
      },
		}
	}
</script>


<style scoped>
	.bottom_btn{
		padding: 30px 15px 0 15px;
	}
</style>



